.container-fluid {
    width: 100%;
    padding: 0;
    margin: 0;
}
.adminlayout__wrapper {
    display: flex;
    width: 100%;
    align-items: stretch;
}
.adminlayout__sidebar {
    z-index: 999;
    left: -235px;
    /* min-height: 100vh; */
    max-width: 250px;
    min-width: 250px;
    transition: 0.3s;
    position: fixed;
}
.adminlayout__sidebar.active {
    left: 0;
    /* min-height: 100vh; */
}
.adminlayout__content.active {
    margin-left: 15px;
}
.adminlayout__content {
    width: 100%;
    margin-left: 250px;
    transition: 0.3s;
}
.adminlayout__content__header {
    padding: 0 10px 4px 10px;
}
.adminlayout__content__children {
}
