.row {
    margin: 0px;
}
.sidebar__wrapper .row {
    margin: 0;
}
.line {
    border: 0.5px solid #dfe6e9;
    width: 100%;
    opacity: 50%;
}
.icon__chevron {
    float: right;
}
.sidebar__wrapper {
    width: 98%;
    box-shadow: rgba(149, 157, 165, 0.2) 0px 5px 10px;
    height: 100vh;
    padding: 0;
    margin: 0;
}
.sidebar__title {
    padding: 20px 10px;
    position: relative;
}
.sidebar__title span {
    position: absolute;
    top: 50%;
    left: 100%;
    transform: translate(-50%, -50%);
    cursor: pointer;
    border: 1px solid #000000;
    border-radius: 50%;
    width: 25px;
    height: 25px;
    display: grid;
    place-items: center;
    background: #fff;
}
.sidebar__title span i {
    font-size: 25px;
}
.sidebar__title h5 {
    text-align: center;
    margin: 0 auto;
}
.sidebar .icon_open {
    transform: rotate(0);
    transition: 0.3s;
}

.sidebar.active .icon_open {
    transform: rotate(180deg);
}

.sidebarmenu__wrapper {
    width: 100%;
}
.my-collapsible-container {
    min-height: 50px;
}
.sidebarmenu .my-collapsible {
    border-left: 5px solid #fff;
    padding: 5px 10px;
}
.sidebarmenu .my-collapsible:hover,
.sidebarmenu .my-collapsible.active {
    border-left-color: #7952b3;
    border-left-style: solid;
}
.sidebarmenu .my-collapsible .my-collapsible__content {
    margin-bottom: 5px;
    max-height: 0;
    overflow: hidden;
    transition: 0.3s;
}
.sidebarmenu .my-collapsible.choose .my-collapsible__content {
    max-height: 250px !important;
}
.sidebarmenu .my-collapsible .icon {
    transform: rotate(0);
    transition: 0.3s;
}
.sidebarmenu .my-collapsible.choose .icon {
    transform: rotate(180deg);
}
.btn__toggle {
    width: 100%;
    border: none;
    background: none;
    color: #000000;
    font-size: 15px;
    font-weight: 500;
    text-align: left;
    padding: 5px 0;
}

.subMenu_item__title {
    cursor: pointer;
    padding: 5px 0px 5px 15px;
    color: #000000;
}
.subMenu_item__title.active {
    font-weight: bolder;
}
.subMenu_item:hover {
    text-decoration: none;
    color: #000000;
}
.subMenu_item:hover .subMenu_item__title {
    background: #82ccdd;
}
.titlePages {
    font-size: 11px;
    text-transform: uppercase;
}
.subMenu_item__title {
    font-size: 12px;
    text-transform: capitalize;
}
