.jumbotron {
    background-color: white;
    display: flex;
    flex-direction: column;
    justify-content: center;
    min-height: 100vh;
}
.card {
    padding-top: 25px;
    padding-left: 25px;
    padding-right: 25px;
    border-radius:5px;
    box-shadow: 3px 3px 10px #263238;
}
.buttonOrange {
    width: 100%;
    background: #F77F00;
    border-style: none;
}
.titleHead {
    display: flex;
    flex-direction: column;
    justify-content: center;
    min-height: 100vh;
}
.btn_login, .btn_login:hover{
    text-decoration: none;
}